.subnav {
  background-color: #374151;
  padding: 0.5rem 2rem;



  a {
    color: #d1d5db;
    margin-right: 1rem;
    text-decoration: none;
    font-size: 0.875rem;

    &:hover {
      color: #ffffff;
    }
    &.active {
      color: #2dd4bf;
      border-color: #2dd4bf;
    }
  }
}

ul.no-bullets {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
